/*
* Table
* */
import React from 'react';
import {inject, observer} from "mobx-react";
import {
	AutoComplete,
	Table,
	Input,
	Select,
	DatePicker,
	Popconfirm,
	Form,
	Modal,
	Icon,
	message,
	InputNumber,
	notification,
	Popover
} from 'antd';
import moment from 'moment';
import * as  mobx from 'mobx';
import * as utils from '@/utils/utils';
import {v4 as uuidv4} from 'uuid';
import url from 'url';
import _ from 'lodash';

const { Option } = Select;
const dateFormat = 'YYYY-MM-DD';

const EditableContext = React.createContext();
const EditableRow = ({form, index, ...props}) => (
	<EditableContext.Provider value={form}>
		<tr {...props} />
	</EditableContext.Provider>
);
const EditableFormRow = Form.create()(EditableRow);

/**/
class EditableCell extends React.Component {
	state = {
		editing: true, //false,
		userIds: [],
		detailWorks: [], // 
	};
	toggleEdit = () => {
		const editing = !this.state.editing;
		this.setState({editing}, () => {
			if (editing) {
				this.input.focus();
			}
		});
	};
	save = e => {
		const {record, handleSave} = this.props;
		this.form.validateFields((error, values) => {
			if (error && error[e.currentTarget.id]) {
				return;
			}
			//this.toggleEdit();
			delete values["beginTime"];
			delete values["endTime"];
			handleSave({...record, ...values});
		});
	};

	onBlur = () => {
		//this.toggleEdit();
	}

	handlePositionChange = (value)=>{
		const {record} = this.props;
		this.props.getProductCategoryList(record, value);
	}

	handleProductCategoryChange = (value)=>{
		const {record, handleSave} = this.props;
		this.props.getProductDetailList(value, record)
	}

	handleProductDetailChange=(value) =>{
		const {record, handleSave} = this.props;
		const {detailWorks} = this.state;
		const selectedDetailItem = Array.isArray(detailWorks)?detailWorks.filter(item => item.productDetailId===value):[]

		if(selectedDetailItem.length>0){
			handleSave(Object.assign(record,{"planBeginTime":selectedDetailItem[0].planBeginTime, "planEndTime":selectedDetailItem[0].planEndTime, "productDetail":selectedDetailItem[0].productDetail, "productDetailId":selectedDetailItem[0].productDetailId, "palnProgress": selectedDetailItem[0].palnProgress, "actualProgress":selectedDetailItem[0].actualProgress}));
		}else{
			handleSave(Object.assign(record,{"planBeginTime":0, "planEndTime":0, "productDetail":value, "productDetailId":"", "palnProgress": 0, "actualProgress": 0}));
		}
	}

	handleNumberActualProgressSave = e => {

		const {record, handleSave} = this.props;
		this.form.validateFields((error, values) => {
			if (error) {
				return;
			}
			handleSave(Object.assign(record, {"actualProgress":values.actualProgress}))
		});
	};

	handleNumberPalnProgressSave = e => {
		const {record, handleSave} = this.props;
		this.form.validateFields((error, values) => {
			if (error) {
				return;
			}
			handleSave(Object.assign(record, {"palnProgress":values.palnProgress}))
		});
	};

	handleDateTime=(date)=>{
		const {record, handleSave} = this.props;
		handleSave(Object.assign(record, {"actualBeginTime":date.valueOf()}))
	}

	renderOption=(item)=>{
		return(
			<Option key={item.id} text={item.name}>{item.name}</Option>
		)
	}

	renderOption2=(item)=>{
		return(
			<Option key={item.productDetailId} text={item.productDetailId}>{item.productDetail}</Option>
		)
	}

	onFocus = () => {
		const {record, getDetailWorkByPostionId} = this.props;
		getDetailWorkByPostionId(record["positionId"]).then((res)=>{
			this.setState({
				detailWorks: Array.isArray(res)?res:[]
			});
		});
	}

	renderCell = form => {
		this.form = form;
		const {children, dataIndex, record, title,  handleDelete, positionList, dailyDate, tableType} = this.props;
		const {editing, detailWorks} = this.state;
		if (dataIndex == 'position') {  // 
			return editing ? (
				<Form.Item style={{margin: 0}}>
					{form.getFieldDecorator(dataIndex, {
						rules: [{
							required: tableType==="today"?true:false,
							message: ``,
						}],
						initialValue: record['positionId'] == null ? undefined : record['positionId']===''?record['position']:record['positionId'],
					})(
						<div className="w clearfix">
							<Popconfirm title="?" onConfirm={() => handleDelete(record)}>
								<Icon
									style={{
										width: '30px',
										color: '#F17E7E',
										fontSize: '15px',
									}}
									type="minus-circle"/>
							</Popconfirm>
							<AutoComplete
								defaultValue={record[dataIndex]}
								dataSource={positionList.map(this.renderOption)}
								placeholder={''}
								style={{ width: "140px" }}
								onChange={this.handlePositionChange}
							/>
						</div>
					)}
				</Form.Item>
			) : (
				<div className="w clearfix">
					<Popconfirm title="?" onConfirm={() => handleDelete(record)}>
						<Icon
							style={{
								width: '35px',
								color: '#F17E7E',
								fontSize: '22px',
								float: 'left'
							}}
							type="minus-circle"/>
					</Popconfirm>
					<div
						className="editable-cell-value-wrap"
						style={{
							float: 'right',
							width: 'calc(100% - 35px)',
							minHeight: '31px'
						}}
						onClick={this.toggleEdit}
					>
						{children}
					</div>
				</div>
			);
		} else if (dataIndex == 'productCategory'  ) { // 
			return editing ? (<Form.Item style={{margin: 0}} className="tfc">
					{form.getFieldDecorator(dataIndex, {
						rules: [{
							required: tableType==="today"?true:false,
							message: ``,
						}],
						initialValue: record['productCategoryId'] == null ? undefined : record['productCategoryId'] == ""?record['productCategory']:record['productCategoryId'],
					})(
						<AutoComplete
							defaultValue={record[dataIndex]}
							dataSource={Array.isArray(record['productCategoryList'])?record['productCategoryList'].map(this.renderOption):[]}
							placeholder={''}
							style={{ width: "140px" }}
							onChange={this.handleProductCategoryChange}
						/>
					)}
				</Form.Item>
			) : (<div
					className="editable-cell-value-wrap"
					style={{minHeight: '31px'}}
					onClick={this.toggleEdit}
				>
					{record[dataIndex]==""||record[dataIndex]==null?"--":record[dataIndex]}
				</div>
			);
		} else if (dataIndex == 'productDetail') { // 
			return editing ? (<Form.Item style={{margin: 0}} className="tfc">
					{form.getFieldDecorator(dataIndex, {
						rules: [{
							required: tableType==="today"?true:false,
							message: ``,
						}],
						initialValue: record['productDetail'],
						//initialValue: record['productDetailId'] == null ? undefined : record['productDetailId'] == ""?record['productDetail']:record['productDetailId'],
					})(
						<div>
							<AutoComplete
								value={record['productDetail']}
								disabled={record["position"]==""||record["position"]==null?true:false}
								onFocus={this.onFocus}
								//dataSource={Array.isArray(record['productDetailList'])?record['productDetailList'].map(this.renderOption2):[]}
								dataSource={Array.isArray(detailWorks)?detailWorks.map(this.renderOption2):[] }
								placeholder={''}
								style={{ width: "140px" }}
								onChange={this.handleProductDetailChange}
							/>
						</div>
					)}
				</Form.Item>
			) : (<div
					className="editable-cell-value-wrap"
					style={{minHeight: '31px'}}
					onClick={this.toggleEdit}
				>
					{record[dataIndex]==""||record[dataIndex]==null?"--":record[dataIndex]}
				</div>
			);
		}else if ( dataIndex == 'planBeginTime'  || dataIndex == 'planEndTime' ) { // 
			return editing && false ? (<Form.Item style={{margin: 0}} className="tfc">
					{form.getFieldDecorator(dataIndex, {
						rules: [{
							required: false,
							message: `${title}`,
						}],
						initialValue: record[dataIndex] == null || record[dataIndex] == 0 ? "" : record[dataIndex],
					})(
						<Input
							ref={node => (this.input = node)}
							placeholder=""
						/>
					)}
				</Form.Item>
			) : (<div
					style={{minHeight: '31px'}}
				>
					{record[dataIndex]==""||record[dataIndex]==null?"--":moment(record[dataIndex]).format(dateFormat)}
				</div>
			);
		}else if ( dataIndex == 'actualBeginTime') { // 
			return editing ? (<Form.Item style={{margin: 0}} className="tfc">
					{form.getFieldDecorator(dataIndex, {
						rules: [{
							required: tableType==="today"?true:false,
							message: `${title}`,
						}],
						initialValue: record[dataIndex] == null || record[dataIndex] == 0 ? moment() : moment(record[dataIndex]),
					})(
						<DatePicker
							style={{width:'122px'}}
							placeholder=""
							format="YYYY-MM-DD"
							onChange={this.handleDateTime}
						/>
					)}
				</Form.Item>
			) : (<div
					style={{minHeight: '31px'}}
				>
					{record[dataIndex] == null || record[dataIndex] == 0 ? dailyDate : moment(record[dataIndex]).format(dateFormat)}
				</div>
			);
		}else if ( dataIndex == 'actualProgress') { // 
			return editing ? (<Form.Item style={{margin: 0}} className="tfc">
					{form.getFieldDecorator(dataIndex, {
						rules: [{
							required: tableType==="today"?true:false,
							message: ``,
						}],
						initialValue: record[dataIndex] == null || record[dataIndex] == 0 ? 0 : record[dataIndex],
					})(
						<div style={{display: "inline-block"}}>
							<InputNumber
								min={0}
								max={100}
								ref={node => (this.input = node)}
								onPressEnter={this.handleNumberActualProgressSave}
								onBlur={this.handleNumberActualProgressSave}
								value={record[dataIndex] == null || record[dataIndex] == 0 ? 0 : record[dataIndex]}
							/>
						</div>
					)} %
				</Form.Item>
			) : (<div
					className="editable-cell-value-wrap"
					style={{minHeight: '31px'}}
					onClick={this.toggleEdit}
				>
					{children}%
				</div>
			);
		} else if ( dataIndex == 'palnProgress') { // 
			return editing ? (<Form.Item style={{margin: 0}} className="tfc">
					{form.getFieldDecorator(dataIndex, {
						rules: [{
							required: false,
							message: `${title}`,
						}],
						initialValue: record[dataIndex] == null || record[dataIndex] == 0 ? 0 : record[dataIndex],
					})(
						<div style={{display: "inline-block"}}>
							<InputNumber
								min={0}
								max={100}
								ref={node => (this.input = node)}
								onPressEnter={this.handleNumberPalnProgressSave}
								onBlur={this.handleNumberPalnProgressSave}
								value={record[dataIndex] == null || record[dataIndex] == 0 ? 0 : record[dataIndex]}
							/>
						</div>
					)} %
				</Form.Item>
			) : (<div
					className="editable-cell-value-wrap"
					style={{minHeight: '31px'}}
					onClick={this.toggleEdit}
				>
					{children}%
				</div>
			);
		} else {
			return editing ? (<Form.Item style={{margin: 0}}>
					{form.getFieldDecorator(dataIndex, {
						rules: [{
							required: false,
							message: `${title}`,
						}],
						initialValue: record[dataIndex] == null || record[dataIndex] == 0 ? 0 : record[dataIndex],
					})(
						<InputNumber
							min={0}
							max={100}
						/>
					)}
				</Form.Item>
			) : (<div
					className="editable-cell-value-wrap"
					style={{minHeight: '31px'}}
					onClick={this.toggleEdit}
				>
					{children}%
				</div>
			);
		}
	};

	render() {
		const {
			editable,
			dataIndex,
			title,
			record,
			index,
			handleSave,
			children,
			...restProps
		} = this.props;
		return (
			<td {...restProps}>
				{editable ? (
					<EditableContext.Consumer>{this.renderCell}</EditableContext.Consumer>
				) : (
					children
				)}
			</td>
		);
	}
}

/*
* table
* */
@observer
class EditableTable extends React.Component {
	constructor(props) {
		super(props);
		this.store = this.props.store;
		this.state = {
			dataSource: [],  // 
			count: 0,        // tablekey
			primaryData: {
				"id": "",
				"position": "",
				"positionId": "",
				"productCategory": "",
				"productDetail": "",
				"specifications": "",
				"unit": "",
				"palnProgress": 0,
				"actualProgress": 0,
				"type": this.props.tableType==='today'?1:0,
			}, // 
			expandedKeys: [], // 
			keys: [], // 
			subtasks: [],
			activeObj: {}, // 
			activeSub: [], // 
			subTaskDis: false, // 
		};
	}

	componentWillReceiveProps(nextProps, nextContext) {
		if (nextProps.dataSource != this.props.dataSource) {
			this.setState({
				dataSource: nextProps.dataSource == null || nextProps.dataSource.length == 0 ? [] : nextProps.dataSource.map((item, index) => {
					return Object.assign(item, {key: index})
				}),
				count: nextProps.dataSource == null || nextProps.dataSource.length == 0 ? 0 : nextProps.dataSource.length,
				primaryData: Object.assign({}, {
					"id": "",
					"position": "",
					"positionId": "",
					"productCategory": "",
					"productDetail": "",
					"specifications": "",
					"unit": "",
					"palnProgress": 0,
					"actualProgress": 0,
					"type": this.props.tableType==='today'?1:0,
				}) // 
			})
		}
	}

	componentDidMount() {
		this.setState({
			dataSource: this.props.dataSource ===undefined || this.props.dataSource == null || this.props.dataSource.length == 0 ? [] : this.props.dataSource.map((item, index) => {
				return Object.assign(item, {"key": index})
			}),
			count: this.props.dataSource ===undefined ||  this.props.dataSource === null || this.props.dataSource.length == 0 ? 0 : this.props.dataSource.length,
			primaryData: Object.assign({}, {
				"id": "",
				"position": "",
				"positionId": "",
				"productCategory": "",
				"productDetail": "",
				"specifications": "",
				"unit": "",
				"palnProgress": 0,
				"actualProgress": 0,
				"type": this.props.tableType==='today'?1:0,
			}), // 
			expandedKeys: [], // 
			keys: [], // 
			subtasks: [],
		});
		this.getPositionList();
	}

	componentWillUnMount() {

	}

	getPositionList = ()=>{
		let hash = window.location.hash;
		const projectId = hash.split("projectId=")[1];
		if(projectId===undefined) {
			return
		}else{
			this.store.getDailyObjList({
				"dailyId":projectId,
				"type":0
			}).then((res)=>{
				this.store.positionList = res;
			})
		}
	}


	getProductCategoryList = (record, value)=>{
		const { positionList } = this.store;
		const selectedItem = positionList.filter(item => item.id===value)
		if(selectedItem.length>0){
			this.store.getDailyObjList({
				"dailyId":value,
				"type":1
			}).then((res)=>{
				this.store.productCategoryList = res;
				if (record["positionId"]==selectedItem[0].id) {
					this.handleSave(Object.assign(record,{"position":selectedItem[0].name,"positionId":selectedItem[0].id, "productCategoryList":res}));
				} else {  // 
					this.handleSave(Object.assign(record,{"position":selectedItem[0].name,"positionId":selectedItem[0].id, "productCategoryList":res,
						productDetail: "", planBeginTime: null, planEndTime: null, palnProgress: 0, actualProgress: 0
					}));
				}
			})
		}else{
			this.handleSave(Object.assign(record,{"position":value,"positionId":"", "productCategoryList":[]}));
		}
	}


	getProductDetailList = (value, record)=>{
		const {projectDetail } = this.store;
		this.store.getDailyContentDetailList({
			"dailyId":value,
			"type":record['type']===1?0:1 //record type 1 type01...
		}).then((res)=>{
			if(res.length>0){
				const selectedItem = record['productCategoryList'].filter(item => item.id===value)
				this.handleSave(Object.assign(record,{"productCategory":selectedItem[0].name, "productCategoryId":selectedItem[0].id, "productDetailList":res}));
				if(record['type']===1){
					this.store.getDailyMaterialList({"dailyId":projectDetail.id,"dcs":res}).then((res)=>{
						this.store.dailyMaterialList = res;
					})
				}
			}else{
				this.handleSave(Object.assign(record,{"productCategory":value, "productCategoryId":"", "productDetailList":[]}));
			}

		})
	}

	// 
	handleDelete = (record) => {
		const {id, key} = record;
		const dataSource = [...this.state.dataSource];
		this.setState({
			dataSource: dataSource.filter(item => item.key !== key)
		}, () => {
			this.props.saveData(this.state.dataSource, key);
		});
	};

	// 
	handleAdd = () => {
		const {dataSource, primaryData, count} = this.state;
		// const newData = Object.assign(primaryData, {key: count, id:"!"+uuidv4()});
		const newData = Object.assign(primaryData, {key: count});
		this.setState({
			dataSource: [...dataSource, newData],
			count: count + 1,
		}, () => {
			this.props.saveData(this.state.dataSource);
		});
	};

	// 
	handleSave = row => {
		const newData = [...this.state.dataSource];
		const index = newData.findIndex(item => row.key === item.key);
		const item = newData[index];
		newData.splice(index, 1, {
			...item,
			...row,
		});
		this.setState({dataSource: newData}, () => {
			// 
			this.props.saveData(this.state.dataSource);
		});
	};

	// 
	saveExpandedRows = (key) => {
		let _this = this;
		const {keys} = _this.state;
		if (keys.indexOf(key) >= 0) {
			keys.splice(keys.indexOf(key), 1);
		} else {
			keys.push(key);
		}
		_this.setState({
			expandedKeys: keys
		}, () => {
			let obj = {[_this.props.id]: keys};
			let expandedKeys = JSON.parse(JSON.stringify(_this.store.expandedKeys));
			_this.store.expandedKeys = Object.assign(expandedKeys, obj);
		});
	}

	// 
	handleView = (record) => {
		this.setState({
			subTaskDis: true, // 
		}, () => {
			this.saveExpandedRows(record);
		});
	}


	// 
	onCalac = (key) => {
		this.saveExpandedRows(key);
		this.store.isEdit = false;
		// tree
		let treeData = JSON.parse(JSON.stringify(this.store.treeData));
		this.store.treeData = utils.updateTreeData(treeData, "update", this.state.activeObj);
		this.subtask.clearState();
		this.setState({
			activeObj: Object.assign({})
		});
	}

	// 
	onRow = (record) => {
		const {disabled} = this.props;
		let subTasks = record.children == null ? [] : record.children.filter((item) => {
			return item.treeType === 'subtask'
		})
		const subNumber = subTasks == null || subTasks.length == 0 ? 0 : subTasks.length
		if (disabled && subNumber > 0) {  // 
			this.handleView(record.id)
		}
	}

	// 
	getDetailWorkByPostionId = (param) => {
	  const {dailyDate} = this.props;
      const params = {
		  positionId: param,
		  dateTime: dailyDate==null||dailyDate==""?0:moment(dailyDate).valueOf()
	  }
	 return this.store.getDetailWorkByPostionId(params);
	}

	render() {
		const {dataSource, expandedKeys} = this.state;
		const {disabled, tableType, dailyDate} = this.props;
		const { positionList, productCategoryList, productDetailList } = this.store;
		const components = {
			body: {
				row: EditableFormRow,
				cell: EditableCell,
			},
		};
		let columnEle = [
			{
				title: (<span><span></span><span style={tableType==="today"?{color:"red"}:{display:"none"}}>*</span></span>),
				dataIndex: 'position',
				width: '14%',
				align: "left",
				editable: !disabled,
				/*render: (text, record) => {
					return (<div
						id={`${record.id}`}
						onClick={this.onRow.bind(this, record)}
						style={{width: '100%', height: '46px', lineHeight: '46px'}}
						className="pr">
						{text}
					</div>);
				}*/
			},
			/*{
				title: (<span><span></span><span style={tableType==="today"?{color:"red"}:{display:"none"}}>*</span></span>),
				dataIndex: 'productCategory',
				width: '14%',
				align: "center",
				editable: !disabled,
			},*/
			{
				title: (<span><span></span><span style={tableType==="today"?{color:"red"}:{display:"none"}}>*</span></span>),
				dataIndex: 'productDetail',
				width: '14%',
				editable: !disabled,
				align: "center",
			},
			{
				title: '',
				dataIndex: 'planBeginTime',
				width: '130px',
				editable: !disabled,
				align: "center",
				render: (text, record) => {
					return (<div
						id={`${record.id}`}
						onClick={this.onRow.bind(this, record)}
						style={{width: '100%', height: '46px', lineHeight: '46px'}}
						className="pr">
						{text===null || text===0|| text==="0"?"":moment(text).format(dateFormat)}
					</div>);
				}
			},
			{
				title: '',
				dataIndex: 'planEndTime',
				width: '130px',
				editable: !disabled,
				align: "center",
				render: (text, record) => {
					return <div
						style={{width: '100%', height: '46px', lineHeight: '46px'}}
						onClick={this.onRow.bind(this, record)}
					>{text===null || text===0|| text==="0"?"":moment(text).format(dateFormat)}</div>
				}
			}

		];
		if(tableType==='today'){
			columnEle = columnEle.concat([
		  /*  {
				title: '',
				dataIndex: 'actualBeginTime',
				width: '130px',
				editable: !disabled,
				align: "center",
				render: (text, record) => {
					return (<div
						id={`${record.id}`}
						onClick={this.onRow.bind(this, record)}
						style={{width: '100%', height: '46px', lineHeight: '46px'}}
						className="pr">
						{text===null || text===0|| text==="0"?"":moment(text).format(dateFormat)}
					</div>);
				}
			},*/
				{
					title: '',
					dataIndex: 'palnProgress',
					width: '160px',
					editable: !disabled,
					align: "center",
					render: (text, record) => {
						return (<div
							id={`${record.id}`}
							onClick={this.onRow.bind(this, record)}
							style={{width: '100%', height: '46px', lineHeight: '46px'}}
							className="pr">
							{text}%
						</div>);
					}
				},
			{
				title: (<span><span></span><span style={tableType==="today"?{color:"red"}:{display:"none"}}>*</span></span>),
				dataIndex: 'actualProgress',
				width: '160px',
				editable: !disabled,
				align: "center",
				render: (text, record) => {
					return (<div
						id={`${record.id}`}
						onClick={this.onRow.bind(this, record)}
						style={{width: '100%', height: '46px', lineHeight: '46px'}}
						className="pr">
						{text}%
					</div>);
				}
			}])
		}
		if(tableType==='tomorrow'){

			columnEle = columnEle.concat([
			{
				title: '',
				dataIndex: 'palnProgress',
				width: '160px',
				editable: !disabled,
				align: "center",
				render: (text, record) => {
					return (<div
						id={`${record.id}`}
						onClick={this.onRow.bind(this, record)}
						style={{width: '100%', height: '46px', lineHeight: '46px'}}
						className="pr">
						{text}%
					</div>);
				}
			}])
		}

		const columns = columnEle.map(col => {
			if (!col.editable) {
				return col;
			}
			return {
				...col,
				onCell: record => ({
					record,
					editable: col.editable,
					dataIndex: col.dataIndex,
					title: col.title,
					handleSave: this.handleSave,
					handleDelete: this.handleDelete,
					getProductCategoryList:this.getProductCategoryList,
					getProductDetailList:this.getProductDetailList,
					getDetailWorkByPostionId: this.getDetailWorkByPostionId,
					positionList:positionList,
					productCategoryList:productCategoryList,
					productDetailList:productDetailList,
					dailyDate:dailyDate,
					tableType:tableType,
				}),
			};
		});
		return (
			<div>
				<Table
					components={components}
					rowClassName={() => 'editable-row'}
					className="expand_table bpm_table"
					bordered={false}
					dataSource={dataSource}
					columns={columns}
					pagination={false}
					size={"middle"}
					expandRowByClick={true}
					expandIconAsCell={false}
					expandIconColumnIndex={-1}
					expandedRowKeys={expandedKeys}
					rowKey={row => row.id}
				/>
				{disabled ? null :
					<Icon onClick={this.handleAdd} style={{margin: '16px 0', color: '#4BA4BE', fontSize: '15px'}}
					      type="plus-circle"/>}
			</div>
		);
	}
}

export default EditableTable;




